.experience {
  padding: var(--vertical-gutter) 0;
  display: grid;
  grid-template-columns: var(--horizontal-gutter) 1fr 1fr 1fr var(
      --horizontal-gutter
    );
  grid-template-rows: repeat(4, auto);
  grid-template-areas:
    ". header header header ."
    "work-items work-items work-items work-items work-items"
    ". . indicators work-item-index .";
  position: relative;
  background-color: #e0e5df;
}

.experience__header {
  grid-area: header;
  align-self: end;
  display: block;
}

.experience__work-item-index {
  grid-area: work-item-index;
  align-self: center;
  justify-self: flex-end;
  font-size: 1.3em;
  font-weight: bold;
}

.work-items {
  -ms-scroll-snap-type: x mandatory;
      scroll-snap-type: x mandatory;
  overflow-x: scroll;
  grid-area: work-items;
  display: grid;
  grid-gap: calc(var(--horizontal-gutter) / 2);
  grid-template-columns: calc(var(--horizontal-gutter) / 2) repeat(
      4,
      calc(100% - 2 * var(--horizontal-gutter))
    ) calc(var(--horizontal-gutter) / 2);
  grid-template-rows: minmax(150px, 1fr);
  /* hide scrollbar on FireFox */
  scrollbar-width: none;
  -ms-overflow-style: none;
  -webkit-overflow-scrolling: touch;
}

/* hide scrollbar on Chrome */
.work-items::-webkit-scrollbar {
  display: none;
}

.work-items::before,
.work-items::after {
  content: "";
}

.scroll-button {
