  /* margin: 0; */
}

.loading__bottom-border {
  height: var(--bottom-border-height);
  background-color: #50366b;
  -webkit-animation: blink 0.5s ease infinite;
          animation: blink 0.5s ease infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}


.loading--done {
  -webkit-transform: scaleY(0.3) translateY(-100vh);
          transform: scaleY(0.3) translateY(-100vh);
  -webkit-transform-origin: center top;
          transform-origin: center top;
  -webkit-transition: -webkit-transform var(--my-timing-function) 2s 1.5s;
  transition: -webkit-transform var(--my-timing-function) 2s 1.5s;
  transition: transform var(--my-timing-function) 2s 1.5s;
  transition: transform var(--my-timing-function) 2s 1.5s, -webkit-transform var(--my-timing-function) 2s 1.5s;
}

.loading--done .loading__contents {
  -webkit-transform: translateY(-100vh);
          transform: translateY(-100vh);
  -webkit-transition: -webkit-transform var(--my-timing-function) 7s 1.5s;
  transition: -webkit-transform var(--my-timing-function) 7s 1.5s;
  transition: transform var(--my-timing-function) 7s 1.5s;
  transition: transform var(--my-timing-function) 7s 1.5s, -webkit-transform var(--my-timing-function) 7s 1.5s;
}

.loading--done .loading__welcome {
  -webkit-animation: fadeIn 1s 0.2s ease;
          animation: fadeIn 1s 0.2s ease;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.loading--done .loading__bottom-border {
  -webkit-animation: load-complete 0.2s ease;
          animation: load-complete 0.2s ease;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

